.scroll-to-top {
    position: fixed;
    bottom: 20px;
    right: 20px;
  
   
 
    padding: 10px;
   
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    z-index: 1000;
  }
  
 
  .scroll-to-top span {
    font-size: 14px;
  }
  